<style>:root{--circle-radius:1.6em;--header-font-height:24pt;--header-span-height:calc(var(--header-font-height)*5/6);--header-padding-top:calc(var(--header-font-height)*1/6 - 2)}.circle{background-color:#fff;color:#000;padding:0;margin:0;border-radius:50%;text-align:center;width:var(--circle-radius);height:var(--circle-radius);font-size:42pt;font-weight:700;border:1.5pt solid #000;z-index:1000}input[type=number]:focus{outline:none}input::-webkit-inner-spin-button,input::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}.refresh-label{width:auto;font-family:Barlow Condensed,sans-serif;font-weight:900;text-transform:uppercase;margin:0;text-align:left;position:absolute;bottom:-4px;left:-2.3em;display:inline-block}.refresh-label,.refresh-points{float:left}.refresh-points{position:absolute;width:auto;left:.3em;top:-1.3em}.fate-label{width:50%;font-family:Barlow Condensed,sans-serif;font-weight:900;text-transform:uppercase;margin:0;right:-1.2em;bottom:-4px;text-align:right;position:absolute;display:block}.fate-label,.fate-points{float:right}.fate-points{position:absolute;width:auto;right:.5em;top:-.7em}.header{font-family:Barlow Condensed,sans-serif;font-size:var(--header-font-height);background-color:var(--header-primary-background-color);color:var(--header-primary-color);text-align:left;position:relative;text-transform:uppercase;margin-top:32pt;margin-bottom:32pt}.header,.header span{height:var(--header-span-height)}.header span{box-sizing:border-box;width:100%;font-weight:900}.header:after{clear:both;display:block}</style>

<script>
    export let refresh=3;
    export let fate=3;
</script>


<div class="header" >
