<style>.td.border{border-right:2pt solid #000}@media print{.character-sheet{border-collapse:collapse;margin:0!important;padding:0!important;height:100%!important;width:auto!important;box-shadow:none!important}}.character-sheet{margin-top:-.25in!important}</style>

<script>
    import PageHeading from "./PageHeading";
    import Aspects from "./Aspects";
    import Vitals from "./Vitals";
    import Skills from "./Skills";
    import Stunts from "./Stunts";
    import RefreshAndFate from "./RefreshAndFate";
    export let character;
</script>

<div class="character-sheet">
    <PageHeading bind:name="{character.name}"/>
    <div class="table">

        <div class="tr">
            <div class="td border">
                <Aspects bind:aspects="{character.aspects}"/>
            </div>
            <div class="td">
                <Vitals bind:vitals="{character.vitals}" bind:will={character.skills[18].value} bind:physique={character.skills[12].value}/>
            </div>
        </div>

        <div class="tr">
            <div class="td border">
                <Stunts bind:stunts="{character.stunts}"/>
                <RefreshAndFate bind:fate="{character.fate}" bind:refresh="{character.refresh}"/>
            </div>
            <div class="td">
                <Skills bind:skills={character.skills}/>
            </div>
        </div>
    </div>
</div>