<style>.aspect-label{font-family:Gothic A1 Black,sans-serif;text-transform:uppercase;width:100%;display:block;font-size:10pt;font-weight:900}.aspect{width:100%;margin-bottom:1em}textarea{padding:.15em;font-size:10pt;border:none;overflow:hidden;resize:none;display:block;width:100%;font-family:Barlow,sans-serif}</style>

<script>
    import Section from '../Section';
    export let aspects={
